@import url(https://fonts.googleapis.com/css2?family=DM+Sans:ital,wght@0,400;0,700;1,400;1,700&family=Nunito+Sans:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
.Cart_ul {
  list-style-type: none;
}

.Cart_image {
  height: 150px;
  width: 150px;
  border-radius: 5px;
}

.Cart__button {
  outline: none;
  border: none;
  border-radius: 5px;
  height: 35px;
  width: 200px;
  font-size: 15px;
}

.ItemCount {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  border-radius: 5px;
  height: 35px;
}

.ItemCount__add,
.ItemCount__button {
  background-color: lightslategray;
  color: whitesmoke;
}

.ItemCount__button {
  height: 25px;
  width: 35px;
  outline: none;
  cursor: pointer;
  border: none;
  border-radius: 15px;
  font-size: 30px;
  font-weight: bolder;
  line-height: 0px;
  padding: 0;
  display: table-cell;
  vertical-align: middle;
}

.ItemCount__counter {
  font-size: 20px;
  margin-bottom: 0;
}

.ItemCount__add {
  outline: none;
  border: none;
  border-radius: 5px;
  height: 35px;
  width: 200px;
  font-size: 15px;
}

.ItemDetail__link {
  margin: 0 auto;
  font-size: large;
  font-weight: bold;
  display: inline-block;
  text-align: center;
  width: 100%;
  color: #a19ec7;
  text-decoration: none;
}

.ItemDetail__description {
  min-height: 25vh;
}

.ItemDetail__image {
  border-radius: 40px;
}

.ItemDetailContainer {
  min-height: 68vh;
}

.ItemCard {
  width: 250px;
  height: 350px;
  border-radius: 10px;
  padding: 10px;
  border: 1px solid lightgray;
}

.ItemCard__title {
  margin: 0;
  font-style: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.ItemCard__image {
  width: 225px;
  height: 200px;
  display: block;
  border-radius: 3px;
}

.ItemCard__price {
}

.ItemCard__detail {
  color: #488ca7;
  width: 100%;
  text-decoration: none;
}

a:hover,
a:active {
  color: cyan;
}

.ItemList {
  display: grid;
  grid-template-columns: repeat(auto-fit, 300px);
  grid-gap: 16px;
  gap: 16px;
  place-items: start start;
  place-content: start start;
}

.ItemListContainer {
  min-height: 68vh;
}

.main {
  display: block;
  height: 60vh;
  width: 100%;
  position: relative;
  letter-spacing: -0.02em;
  color: whitesmoke;
}

.main::before {
  content: "";
  position: absolute;
  object-fit: cover;
  object-position: top center;
  background-size: cover;
  top: 0;
  left: 0;
  width: 100%;
  height: 60vh;
  background-image: url("https://images.pexels.com/photos/70912/pexels-photo-70912.jpeg?auto=compress&cs=tinysrgbg");
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
  background-attachment: fixed;
  -webkit-filter: blur(2px) brightness(0.4);
          filter: blur(2px) brightness(0.4);
}

.main-inside {
  height: 60vh;
  position: relative;
}

.CartWidget {
  position: relative;
}

.CartWidget__image {
  width: 32px;
  -webkit-filter: invert();
          filter: invert();
}

.CartWidget__counter {
  position: absolute;
  display: inline-block;
  margin: 0;
  bottom: 0;
  right: 0;
  height: 12px;
  width: 4px;
  color: whitesmoke;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Nunito Sans", sans-serif;
  padding-bottom: 2vh;
}

h1,
h2,
h3 {
  font-family: "DM Sans", sans-serif;
}

